import React from "react";
import { graphql, Link } from "gatsby";

import Layout from "../components/Layout";
import { Row, Col } from "antd";

const IndexPage = ({ data }) => {
  const detail = data.contentfulPortfolio;
  console.log(detail);

  return (
    <Layout>
      <article id="main">
        <header
          style={{
            backgroundImage:
              detail.portfolioAssets[0].file.url !== ""
                ? `linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${detail.portfolioAssets[0].file.url})`
                : "inherit",
          }}
        >
          <h2>{detail.portfolioTitle}</h2>
        </header>
        <section className="wrapper style5">
          <div className="inner">
            <div style={{ minHeight: "80vh" }}>
              <p>{detail.portfolioDescription.portfolioDescription}</p>
            </div>

            <Row>
              <Col xs={24} sm={12}>
                <Link role="button" className="button" to={`/portfolio/`}>
                  See Other Portfolio
                </Link>
              </Col>

              {detail.portfolioUrl && (
                <Col className="text-right" xs={24} sm={12}>
                  <a
                    href={detail.portfolioUrl}
                    className="button primary block"
                    target="_blank"
                    rel="noreferrer"
                  >
                    View Website
                  </a>
                </Col>
              )}
            </Row>
          </div>
        </section>
      </article>
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query($id: String) {
    contentfulPortfolio(id: { eq: $id }) {
      id
      portfolioAssets {
        file {
          url
          fileName
        }
      }
      portfolioDescription {
        portfolioDescription
      }
      portfolioTitle
      portfolioUrl
      slug
    }
  }
`;
